<template>
    <div class="header-container">
        <div class="header-content-mobile" v-if="!isPc">
            <div class="header-menu">
                <a class="a-logo" @click="enterHomePage()"></a>
                <button class="hamburger hamburger--squeeze" type="button" @click="openMenu" :class="{'is-active': isOpen}">
                    <span class="hamburger-box">
                      <span class="hamburger-inner"></span>
                    </span>
                </button>
            </div>
            <div class="menu-content" id="menu-content" :style="{display: isOpen ? 'flex': 'none'}">
                <a class="header-link" @click="switchCatalogue(index)" v-for="(item, index) in data" :key="index">{{item.name}}</a>
                <a-radio-group class="lang-switch" v-model="langMb">
                    <a-radio :value="item.value === lang ? 1 : 0" v-on:click="switchLang(item)"
                             v-for="(item,index) in langList" :key="index">{{item.key}}</a-radio>
                </a-radio-group>
                <p class="contact-us" @click="openContact()">{{$t('help.Button')}}</p>
            </div>
        </div>
    </div>
</template>

<script>
    import {AU_EXO_TRACKER, AU_UUID, AU_HMAC, AU_SELECTED_SERVICE, AU_EMAIL, AU_EMAIL_STATUS} from "@/requestServer";
    import {TweenMax} from 'gsap';
    export default {
        name: "Header",
        data() {
            return {
                serviceChildList: [{key:'登出服務',path:'logout'}],
                langList: [{key:'繁体中文',value:'zh-TW'},{key:'简体中文',value:'zh-CN'},{key:'English',value:'en-US'}],
                lang: this.$i18n.locale,
                router: this.$route.path,
                isLogin: localStorage.getItem('uuid') && localStorage.getItem('hmac'),
                selectedService: JSON.parse(localStorage.getItem(AU_SELECTED_SERVICE)),
                isPc : this.$isPc(),
                isOpen: false,
                langMb: 1,
            }
        },
        props: {
            data: {},
        },
        watch: {
            data: {
                immediate: true,
                handler(value) {
                    this.data = value;
                }
            },
            $route(to) {
                if (this.router !== to.path) {
                    this.router = to.path;
                }
                //console.log('to',to);
            },
        },
        mounted() {
            this.isLogin = localStorage.getItem(AU_UUID) && localStorage.getItem(AU_HMAC);
            if (this.isLogin) {
                if (localStorage.getItem(AU_SELECTED_SERVICE)) {
                    this.serviceChildList = [{key:'管理賬戶',path:'user/account',name:'manageAccount'},{key:'管理賬單',path:'invoice',name:'manageBill'},{key:'登出服務',path:'logout'}]
                } else {
                    const that = this;
                    this.$request.auGetAllServices('','',function (data) {
                        if (data && (data.status === 200 || data.status === 201) && data.data) {
                            localStorage.setItem(AU_EMAIL, data.data.email);
                            localStorage.setItem(AU_EMAIL_STATUS, data.data.status);
                            if (data.data.jjService && data.data.jjService.length > 0) {
                                const activeServices = data.data.jjService.filter(service => service.status === 'Active')
                                localStorage.setItem(AU_SELECTED_SERVICE, JSON.stringify(activeServices.length > 0 ? activeServices.pop() : data.data.jjService[0]));
                                that.serviceChildList = [{key:'管理賬戶',path:'user/account',name:'manageAccount'},{key:'管理賬單',path:'invoice',name:'manageBill'},{key:'登出服務',path:'logout'}]
                            } else if (data.data.unpaidService && data.data.unpaidService.length > 0) {
                                localStorage.setItem(AU_SELECTED_SERVICE,JSON.stringify(data.data.unpaidService[0]));
                                that.serviceChildList = [{key:'管理賬戶',path:'user/account',name:'manageAccount'},{key:'管理賬單',path:'invoice',name:'manageBill'},{key:'登出服務',path:'logout'}]
                            }
                        }
                    });
                }
            }
        },
        methods: {
            switchCatalogue(index){
                this.openMenu();
                this.$emit("switchCatalogue", index);
            },
            openContact(){
                this.openMenu();
                this.$emit("isMBOpenContact", true);
            },
            switchLang(lang) {
                if (this.lang !== lang.value){
                    this.lang = lang.value;
                    this.$emit('switchLang',true);
                    this.$setupLang(lang.value);
                }
                if (!this.isPc) {
                    this.isOpen = false
                }
            },
            enterHomePage() {
                if (this.isPc || (!this.isPc && !(this.router.includes('order/') || this.router.includes('user/invoice')))) {
                    if(this.$route.name !== 'product') {
                        if (!(this.$route.name === 'home')) {
                            this.$router.push({
                                path: '',
                                name: 'home',
                                query: this.$route.query
                            });
                        }
                    }
                } else if(!this.isPc && (this.router.includes('order/') || this.router.includes('user/invoice'))) {
                    this.$router.back(-1);
                }
                if (!this.isPc) {
                    this.isOpen = false;
                }
            },
            openMenu() {
                if (this.isOpen) {
                    const that = this;
                    TweenMax.fromTo(
                        document.getElementById('menu-content'),
                        0.3,
                        {right: '0'},
                        {css:{right: '-100%'}, ease: Power0.easeNone, onComplete: function () {
                                that.isOpen = false
                            }}
                    )
                } else {
                    this.isOpen = true;
                    TweenMax.fromTo(
                        document.getElementById('menu-content'),
                        0.3,
                        {right: '-100%'},
                        {right: '0', ease: Power0.easeNone}
                    )
                }

            }
        }
    }
</script>
